<template>
    <div class="add-vehicle-form">
        <label>车辆名称:</label>
        <input v-model="vehicle.name" type="text" placeholder="请输入车辆名称">

        <label>租赁价格:</label>
        <input v-model="vehicle.price" type="text" placeholder="请输入租赁价格">

        <label>车辆类型:</label>
        <input v-model="vehicle.type" type="text" placeholder="请输入车辆类型">

        <label>车辆描述:</label>
        <textarea v-model="vehicle.description" placeholder="请输入车辆描述"></textarea>

        <button @click.prevent="submitForm">确认添加</button>
        <button id="back" @click="back">返回</button>
    </div>
</template>
  
<script>
import axios from 'axios';

export default {
    data() {
        return {
            userID: '',
            vehicle: {
                name: '',
                price: '',
                type: '',
                description: ''
            }
        };
    },
    created() {
        this.userID = localStorage.getItem('userID');
    },
    methods: {
        submitForm() {
            axios.post('/car/addCar', {
                carName: this.vehicle.name,
                carPrice: this.vehicle.price,
                carType: this.vehicle.type,
                carDis: this.vehicle.description,
                userID: this.userID
            })
                .then(response => {
                    console.log('添加成功: ', response.data);
                    alert("'添加成功!")
                    // 在这里添加添加成功后的逻辑
                })
                .catch(error => {
                    console.error('添加失败: ', error);
                    alert("'添加失败!")
                    // 在这里添加添加失败后的逻辑
                });

        },
        back(){
            this.$router.push("/main");
        }
    }
};
</script>
  
<style scoped>
#back{
    background-color: #f55151;
}
.add-vehicle-form {
    display: flex;
    flex-direction: column;
    gap: 1%;
    width: 80%;
    margin: auto;
}

.add-vehicle-form label {
    font-weight: bold;
}

.add-vehicle-form input,
.add-vehicle-form textarea {
    padding: 1%;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 100%;
}

.add-vehicle-form button {
    padding: 1%;
    background-color: #4CAF50;
    color: white;
    border: none;
    margin-top: 3%;
    border-radius: 4px;
    cursor: pointer;
    width: 20%;
    align-self: center;
}

.add-vehicle-form button:hover {
    background-color: #45a049;
}
</style>
  